/*news page*/
.newsimg {
  width: 100%;
  height: 520px;
  position: relative;
}

/*}*/
.bgs_text {
  width: 210px;
  background-color: rgba(0, 0, 0, 0.54);
  height: 90px;
  position: absolute;
  vertical-align: middle;
  /*make position center*/
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.news_event {
  /*background-color:rgba(0,0,0,0.54);*/
  text-align: center;
  position: absolute;
  padding: 25px;
  height: 90px;
  color: orange;
  font-size: 27px;
}

.news_logo {
  clear: both;
  line-height: 15px;
  height: 80px;
  width: auto;
  margin-top: -12px;
}

.news_text_logo {
  color: #66afe9;
  margin-top: 10px;
  font-size: 15px;
}

.news_events {
  color: orange;
  font-size: 27px -2;
}

.follow_lines_news {
  width: 100px;
  height: 90px -89;
  background: orange;
  margin-top: -10px;
}

/*make a div hover on picture*/
/*div on picture on page */
.mypictures {
  width: 100%;
  height: 800px;
  margin-top: 20px;
}

.img-item {
  width: 100%;
  position: relative;
}

.img-item img {
  position: relative;
  width: 100%;
}

.news-img {
  height: 210px;
}

.box-shadow-on-img {
  /*background-image: url("../Picture/web-comp/air-logistic.jpg");*/
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
}

.overlay-img-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(65, 162, 145, 0.4);
  opacity: 0;
  transition: opacity .3s ease;
  z-index: 100;
}

.img-item:hover .overlay-img-box {
  opacity: 1;
}

.time {
  font-size: 27px -7;
}

.month {
  font-size: 27px -13;
}

.pcolor {
  font-size: 17px;
  font-weight: bold;
  margin-top: 10px;
}

.pc, .ibox, .Phone_Number {
  border: gray;
  border-radius: 0;
  padding-top: 5px;
  height: 40px;
}

@media screen and (max-width: 376px) {
  /*style title*/
  .style-yellow {
    font-size: 30px;
    color: #ffd11a;
  }
  /*online */
  .quote_request_lines {
    height: 1px;
    width: 140px;
    background: #ffd11a;
    margin-top: -4px;
  }
  /*style on text input*/
  .pof, .p_d, .commodity {
    padding-left: 13px;
    font-size: 15px;
    color: #838383;
    font-family: "Roboto",-apple-system,BlinkMacSystemFont,"Segoe UI","Helvetica Neue",Arial,sans-serif;
    letter-spacing: 0.2px;
  }
  .pc, .ibox {
    width: 220px;
    margin-left: 5px;
  }
  .myrow {
    padding-left: 25px;
  }
  .mycargo {
    margin-left: 15px;
    color: #838383;
    font-family: "Roboto",-apple-system,BlinkMacSystemFont,"Segoe UI","Helvetica Neue",Arial,sans-serif;
    letter-spacing: 0.2px;
  }
  .Cargo_Weight {
    padding-left: 12px;
    color: #838383;
    font-family: "Roboto",-apple-system,BlinkMacSystemFont,"Segoe UI","Helvetica Neue",Arial,sans-serif;
    letter-spacing: 0.2px;
  }
  .KGs, .remark {
    padding-left: 10px;
    color: #838383;
    font-family: "Roboto",-apple-system,BlinkMacSystemFont,"Segoe UI","Helvetica Neue",Arial,sans-serif;
    letter-spacing: 0.2px;
  }
}

